import React from "react";
import './List.css';


const List= ({people})=>{

    return(
        <>
        {people.map((person) => {
            const {id,name,age,image}=person;
            return(
        
                    <div key={id} className='profil'>
       
                    <img src={image} alt='image profil'/>
    
                    <div >
                        <h5 className='infos'>{name}</h5>
                        <p className='infos'>{age} years</p>
                    </div>

                    </div>

            );
        })
   
    }</>
    )
    
}
export default List;