import './App.css';
import React,{useState} from 'react';
import List from './List';
import data from './data'

function App() {
 const [profils,setProfil]=useState(data);

 const clickButton = ()=>{
   setProfil([]);

 }

  return (
    <main>
      <section className='container'>
        <h3>{profils.length} birthdays today</h3>
        <List people={profils}/>
        <button className='button' onClick={clickButton}>Clear</button>
      </section>
    </main>
  );
}

export default App;
